import React from 'react';
import { Grid, Segment, List } from 'semantic-ui-react';
import { Link } from 'react-router-dom'
import VKM_Logo from '../img/VKM_logo_v5.png'

const FooterSui = () => {

return (
  <>
<Segment vertical className='footerBg'>
  <Grid divided inverted stackable={true} padded centered vertical>
    <Grid.Row>
      <Grid.Column width={4}>
        <List link >
          <List.Item as='a' className='footer-link'>
            <Link to='/' className='footer-link'>Info&amp;Skills</Link></List.Item>
          <List.Item as='a' className='footer-link'><Link to='/projects' className='footer-link'>Projects</Link></List.Item>
         <List.Item as='a' className='footer-link'> <Link to='/contact' className='footer-link'>Contact</Link></List.Item>
          <List.Item as='a' className='footer-link'><Link to={{pathname: 'https://github.com/VKMStudioX'}}  target='_blank' className='footer-link'>GitHub</Link></List.Item>
        </List>
      </Grid.Column>
       <Grid.Column width={4} only='computer wideScreen largeScreen tablet'>
        <div className="Words">
  <div className="Words-line">
    <p>&nbsp;</p>
    <p>Maybe you</p>
  </div>
  <div className="Words-line">
    <p>Maybe you</p>
    <p>want to</p>
  </div>
  <div className="Words-line">
    <p>want to</p>
    <p>help me</p>
  </div>
  <div className="Words-line">
    <p>help me</p>
    <p>realize</p>
  </div>
  <div className="Words-line">
    <p>realize</p>
    <p>my dreams?</p>
  </div>
  <div className="Words-line">
    <p>my dreams?</p>
    <p>&nbsp;</p>
  </div>
</div> 
      </Grid.Column> 
      <Grid.Column width={4} only='computer wideScreen largeScreen'>
     
      
      <div class="ribbon-wrapper"><div class="glow">&nbsp;</div>
		<div class="ribbon-front">
			Copyrighted by &copy; Krzysztof Meyer by 2021.
		</div>
    <img src={VKM_Logo} alt='logo' className='footerImg'/>
	</div>

 
      

      </Grid.Column>
    </Grid.Row>
  </Grid>
</Segment>
</>
)

}

export default FooterSui